import React,{createContext,useReducer} from 'react'

const initialState={
    //a felhasznl ltal megadott adatok:x,y,minesNr kerl majd be a dim objektumba
    dim:{},
    field:{
        isMine:false,
        state: 0,//state: 0-felfedetlen, 1-felfedett, 2-zszl,3-felfedett s felrobbant
        mineNeighbors:0
    },
    board:[],
    stage:0//0-nincs elindtva a jtk, 1- a jtk folyik, 2- vge:felrobbant , 3- vge:gyzelem

}
//ltrehozzuk a globlis state-t
export const MyGlobalContext =createContext(initialState)
//a global state-en a komponensek klnbz tipus mveleteket hajthatnak vgre, definilni kell
// a lehetsges mveleteket, egy olyan fg-t kell ltrehozni melynek 2 bemen paramtere van:
//a current state s az action obj.(ez rja le h hogyan lehet mdostani a state-t)
const MyReducer=(state, action)=>{
    let newBoard=[]
    switch(action.type) {
        case 'SETUP_DIM':
            newBoard=[]
            const {x,y,minesNr}=action.dim
            for(let i=0;i<y;i++){
                    newBoard[i]=[]//els sor lesz a mtrixban
                    for(let j=0;j<x;j++)
                        newBoard[i][j]={ isMine:false,state:0,mineNeighbors:0}
                }
                //< a bombk vletlenszer elhelyezse:
                for(let i=0;i<minesNr;i++){
                    let randX,randY
                    do{
                        randX = Math.floor(Math.random() *x);
                        randY = Math.floor(Math.random() *y);
                        if(newBoard[randY] && newBoard[randY][randX] && !newBoard[randY][randX].isMine)
                            break
                    }while (true)
                    newBoard[randY][randX].isMine=true
                    //ha leraktuk a bombt a szomszdoknak 1-el nvelni kell a mineNeighbors attribtumt
                    for (let i = -1; i <= 1; i++) 
                        for (let j = -1; j <= 1; j++)
                            if (newBoard[i + randY] && newBoard[i + randY][j + randX] && !newBoard[i + randY][j + randX].isMine)
                                newBoard[i + randY][j + randX].mineNeighbors++;        
                }//a bombk vletlenszer elhelyezse >
                return {...state,  dim: action.dim ,board:newBoard, stage:1}   
       /* case 'INITIALIZE_BOARD':*/
            
        case 'REVEAL_FIELD':
            return reveal(state,action.position)
        case 'REVEAL_ALL':
            //console.log('felfed mind')
            newBoard=state.board
            for(let row of newBoard)
               for(let f of row)
                    if(f.state==0) f.state=1
            //console.log(newBoard)
            return {...state,board:newBoard}
        case 'SET_FLAG':        
            const field=state.board[action.position.y][action.position.x];
            field.state=2
            newBoard=state.board
            return  {...state,board:newBoard}
        case 'REMOVE_FLAG':        
            const field_r=state.board[action.position.y][action.position.x];
            field_r.state=0
            newBoard=state.board
            return  {...state,board:newBoard}
        case 'CHECK_VICTORY':
            if (state.stage !== 1) return state;
            const victory = state.board.every(row => 
                    row.every(field =>field.state==1 || field.state === 2))
            return victory? {...state,stage:3} : state
        default:
            return state
    }
}

//ahhoz, h a tbbi komponens hozzfrjen a globlis state-hez szksges egy Provider(szolgltat) komponens:
//mely tovbbtja a globlis llapotot az sszes alrendelt sszetevnek
export const MyGlobalProvider=({children})=>{
    const [state,dispatch]=useReducer(MyReducer,initialState)

     return(
        <MyGlobalContext.Provider value={[state,dispatch]}> 
            {children}
        </MyGlobalContext.Provider>
    )
}

const reveal=(state,position)=>{
    let newBoard=[]
    const {x,y}=position
    const field=state.board[y][x]
    if(field.state==1) 
        return state//nem trtnik semmi ha olyanra kattintunk ami mr fel van fedve
    else if(field.isMine) {
        field.state=3
        newBoard=state.board
        //newBoard[y][x]=field
        console.log('vge')
        return {...state,board:newBoard,stage:2}//vge a jtknak
    }else{
        field.state=1
        newBoard=state.board
        //newBoard[y][x]=field  
    //fel kell fedni az sszes szomszdost ha res-rekurzival
        if (field.mineNeighbors === 0) {//nincs olyan szomszd ahol bomba lenne, ezrt fel kell fedni az sszes szomszdot
            for (let i = -1; i <= 1; i++) {
                for (let j = -1; j <= 1; j++) 
                    if (state.board[y + j] && state.board[y + j][x + i]) {
                        const newPos={x:x+i,y:y+j}
                        reveal({...state,board:newBoard},newPos);
                    }      
            }
            return {...state,board:newBoard}
  }}
}
